import React, {Component} from 'react';

class Pres extends Component{
  render(){
    let presText="Confía en el tiempo, que suele dar dulces salidas a  muchas amargas dificultades";

    return(
      <div className="pres">

        <div id="texto-pres">

        <p>"{presText}"</p>
        <p id="autor">Miguel de Cervantes</p>

        </div>


      </div>

    );

  }

}

export default Pres;



